.login{
  width: 100%;
  height: 100%;
  background-image: url('./images/bg.jpg');
  background-size: 100% 100% ;

  .login-header{
    display: flex;
    align-items: center;
    background-color: rgba(21, 20, 13, 0.5);
    height: 80px;
    img{
      width: 40px;
      height: 40px;
      margin: 0 15px 0 50px;
    }
    h1{
      font-size: 30px;
      color: #fff;
    }

  }

  .login-content{
    position:relative;
    width: 400px;
    height: 300px;
    background-color: #fff;
    margin: 50px auto;
    padding: 20px 40px;
    .error-msg {
      visibility: hidden;
      position: absolute;
      top: 0;
      left: 0;
      text-align: center;
      height: 30px;
      width: 100%;
      background: #f60c1a;
      color: #ffffff;
      font-size: 16px;
      transform: translateY(-30px);
      transition: all .3s;
      &.show {
        visibility: visible;
        transform: translateY(0);
      }}

    h2{
      font-size: 30px;
      font-weight: bold;
      text-align: center;
      margin-bottom: 20px;

    }
    .login-form{
      .login-form-button{
        width: 100%;
      }
    }

  }




}
@primary-color: #1DA57A;